.topbar{
    widows: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    z-index: 99999;
}

.topbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo{
    font-weight: bold;
font-size: 30px;
color: darkblue;
cursor: pointer;
}
.topRight{
    display: flex;
    align-items: center;
}

.topbarIconContainer{
    position: relative;
    cursor: pointer;
    margin-right: 10px;
    color: #555;
}
.topIcinBadge{
    position: absolute;
    top: -5px;
    right:0px;
    background-color: red;
    width: 15px;
    height: 15px;
    color: white;
    border-radius: 50%;
    display: grid;
    place-items: center;
    font-size: 10px;
}
.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}